#particles-js {
	background-color: $green;
	height: $header-height;
	position: absolute;
	top: 0;
	width: 100%;
}

.bg-pattern-green{
	background: $green url('assets/bg-pattern-green.png') repeat 0 0;
}

.error404 {
	.block-bg-overlay {
		background-color: $green;
	}
	#particles-js {
		background-color: transparent;
		height: 700px;
	}
	.limit-width {
		max-width: none !important;
	}
	.main-container .row-container .row-parent {
		padding-top: 0;
		.uncoltable {
			padding-top: 36px;
		}
	}
}

.header-wrapper {
	display: block;
	.row-container {
		.row-inner {
			margin-left: 0;
			> div {
				padding-left: 0 !important;
			}
		}
		.row {
			.uncol {
				text-align: center;
			}
			.uncoltable {
				display: inline-block;
				padding-top: 95px !important;
				width: auto;
			}
		}
	}
}

.main-container {
	.post-wrapper {
		.row-container {
			background-color: white;
		}
	}
}

.home {
	#page-header {
		display: block;
		position: relative;
	}
	#particles-js {
		bottom: 0;
		top: -72px;
	}
	.header-wrapper {
		background-color: $green;
	}
}

ul {
	list-style-type: none;
	li {
		font-size: 18px;
	}
	&.horizontal {
		li {
			display: inline-block;
		}
	}
}

table {
	tbody {
		tr {
			td {
				a {
					border-bottom: 0;
				}
			}
		}
	}
}

table td, 
table th {
	font-weight: normal;
	vertical-align: top;
}

html {
	margin-top: 0 !important;
}
.menu-container {
	top: 0 !important;
}

// div > a {
// 	border-bottom: 0 !important;
// }

a {
	color: $coral;
	&:hover {
		color: darken($coral, 20%);
	}
}

.page-template,
.page-template-default,
.post-template-default {
	#tt #ttcont a {  color: $coral !important; }
} 

.style-light {
	a {
		color: $coral;
		&:not(.btn-text-skin):hover {
			color: darken($coral, 20%) !important;
		}
	}
}

.style-color-wayh-bg {
	background-color: transparent;
}

.btn {
	font-family: $font-sans-bold;
	font-size: 14px;
	letter-spacing: 0;
	margin: 20px 0;
	text-decoration: none;
	&.submit-btn{
		background-color: #3d3030;
		border-color: #3d3030;
		border-radius: 0;
		color: $white;
		font-family: $font-serif-book;
		font-size: 24px;
		font-weight: normal !important;
		letter-spacing: 0;
		line-height: 68px;
		margin: 0;
		max-width: 156px;
		padding: 0 40px !important;
		text-transform: none;
		vertical-align: top;
		&:hover{
			background-color: $white;
			color: #3d3030;
		}
	}
}

input{
	border: 0;
	display: inline-block;
	margin-top: 0;
	padding: 0 15px;
	&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
	  color: #CCC;
	}
	&::-moz-placeholder { /* Firefox 19+ */
	  color: #CCC;
	}
	&:-ms-input-placeholder { /* IE 10+ */
	  color: #CCC;
	}
	&:-moz-placeholder { /* Firefox 18- */
	  color: #CCC;
	}
}

.mc-field-group {
	input {
		&[type="email"]{
			color: $gray33;
			font-family: $font-serif-book;
			font-size: 24px;
			height: 70px;
			line-height: 70px;
			max-width: 448px;
			padding-left: 35px;
			width: 100%;
			vertical-align: top;
		}
	}
}

#contact #mc_embed_signup div.mce_inline_error {
	background-color: transparent;
	color: #FF0000;
}

.media-credit {
	margin-left: 0;
	text-align: left;
}

.comments-area {
	input {
		border: 1px solid #eaeaea;
		line-height: 30px;
	}
}

.pagination {
	a.disabled {
		color: #ccc;
		cursor: default;
	  	pointer-events: none;
	}
}

.widget-container .tagcloud.dark a {
	background-color: $black;
	border-radius: 0;
	border: 0;
	color: $white;
	font-family: $font-serif-book;
    font-size: 13px !important;
    font-weight: normal;
    line-height: 1.5;
    padding: 3px 10px;
    text-transform: capitalize;
}

button.wdt-clear-filters-button {
	background-color: #F3F5F6;;
	font-weight: normal !important;
	&:hover {
		background-color: #F1F1F1;
	}
	&:before {
		padding-right: 6px;
	}
}


//wpdatatables styles
.centre-table {
	margin-top: 40px;
	.wpDataTables {
		label .wdt-checkbox-label {
			font-size: 14px;
		}
		.bootstrap-select.btn-group .dropdown-toggle {
			padding: 0px !important;
			.filter-option {
				color: #000;
				font-weight: normal;
				margin: 0;
				text-align: left;
				width: 90%;
			}
		}
		.bs-caret {
			color: #000;
		}
		.row-detail {
			font-size: 14px;
			ul {
				padding: 10px 18px 18px 18px;
				li {
					font-size: 14px;
					line-height: 18px;
				}
			}
		}
		.wpDataTableFilterSection {
			&:first-child {
				padding-left: 0;
			}
			.bootstrap-select.btn-group .dropdown-menu {
				li {
					margin-bottom: 0;
					a span.text {
						font-size: 14px;
					}
				}
			}
		}
	}

	.wpdt-c {
		input {
			font-weight: normal;
		}
		.dropdown-menu .divider {
		   background-color: rgba(0, 0, 0, 0.2);
		   margin-top: 0px;
		   padding: 0;
		}
		.btn-group.bootstrap-select .inner .text {
			font-size: 15px;
		}
		.bootstrap-select {
			.check-mark {
	   			top: 16px;
			}
			.bs-searchbox:before {
	   			top: 3px;
			}
		}
	}

	.wpDataTablesFilter .wpDataTableFilterSection:not(#wdt-clear-filters-button-block):not(#wdt-pf-search-filters-button-block) {
		margin-bottom: 0;
		min-height: 0;
		padding-bottom: 0;
		width: 33%;
	}

	.wpDataTablesWrapper {
		margin: 0;
		div.dt-buttons {
			margin-bottom: 0;
		}
		.dataTables_length,
		.dataTables_filter {
			label {
				font-size: 15px;
			}
		}
		.dataTables_length {
			float: right;
		}
		.dataTables_filter {
			float: left;
			text-align: left;
		}
		table.wpDataTable td.column-model-name {
			font-weight: 700;
		}
	}
}


//glossary
.glossaryLink {
	border-bottom: dotted 2px !important;
    color: inherit;
    display: inline;
	font-size: inherit;
}

#tt {
	#ttcont {
		font-size: 16px !important;
		padding: 10px !important;
	}
}

@media (max-width: $screen-md) {
	.single {
		.header-wrapper {
			background-color: transparent;
		}
		#particles-js {
			display: none;
		}
	}
	.header-wrapper {
		.row-inner {
			padding: 0 20px;
		}
		.row-container {
			.row {
				.uncoltable {
					padding-top: 75px!important;
				}
			}
		}
	}

	.centre-table {
		.wpDataTablesFilter .wpDataTableFilterSection {
			&#wdt-clear-filters-button-block {
				padding-top: 0;
			}
			&:not(#wdt-clear-filters-button-block):not(#wdt-pf-search-filters-button-block) {
				margin-bottom: 0;
				min-height: 0;
				width: 100%;
			}
		}
		.wpDataTablesWrapper .wpDataTableFilterSection {
			padding-left: 0;
		}
		.bootstrap-select.btn-group .dropdown-menu {
			width: 100%;
		}
		.wpDataTables {
			.wpDataTableFilterSection {
				.bootstrap-select.btn-group .dropdown-menu li a span.text {
					text-align: left;
					white-space: normal;
				}
			}
		}
	}
}

