$font-dir-stag: 'library/fonts/stag/';
$font-dir-brandon: 'library/fonts/brandon/';
$font-dir-gothambold: 'library/fonts/gothambold/';
$font-dir-humanitarianicons: 'library/fonts/humanitarian-icons/';


/////////////////////////
//
//  STAG
//
/////////////////////////
@font-face {
  font-family: 'Stag-Light';
  src: url('#{$font-dir-stag}Stag-Light.eot');
  src: url('#{$font-dir-stag}Stag-Light.woff') format('woff');
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: 'Stag-Book';
  src: url('#{$font-dir-stag}Stag-Book.eot');
  src: url('#{$font-dir-stag}Stag-Book.woff') format('woff');
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: 'Stag-Medium';
  src: url('#{$font-dir-stag}Stag-Medium.eot');
  src: url('#{$font-dir-stag}Stag-Medium.woff') format('woff');
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: 'Stag-Semibold';
  src: url('#{$font-dir-stag}Stag-Semibold.eot');
  src: url('#{$font-dir-stag}Stag-Semibold.woff') format('woff');
  font-style: normal;
  font-stretch: normal;
}

/////////////////////////
//
//  BRANDON
//
/////////////////////////
@font-face {
  font-family: 'Brandon';
  src: url('#{$font-dir-brandon}BrandonGrotesque-Bold.eot');
  src: url('#{$font-dir-brandon}BrandonGrotesque-Bold.woff') format('woff');
  font-style: normal;
  font-stretch: normal;
  font-weight: bold;
}
@font-face {
  font-family: 'Brandon';
  src: url('#{$font-dir-brandon}BrandonGrotesque-Reg.eot');
  src: url('#{$font-dir-brandon}BrandonGrotesque-Reg.woff') format('woff');
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
}

/////////////////////////
//
//  GOTHAM
//
/////////////////////////
@font-face {
  font-family: 'Gotham-Bold';
  src: url('#{$font-dir-gothambold}gothambold.eot');
  src: url('#{$font-dir-gothambold}gothambold.woff') format('woff');
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
}


/////////////////////////
//
//  HUMANITARIAN ICONS
//
/////////////////////////
@font-face {
  font-family: 'Humanitarian-Icons-v02';
  src:  url('#{$font-dir-humanitarianicons}Humanitarian-Icons-v02.eot?d5h7ar');
  src:  url('#{$font-dir-humanitarianicons}Humanitarian-Icons-v02.eot?d5h7ar#iefix') format('embedded-opentype'),
    url('#{$font-dir-humanitarianicons}Humanitarian-Icons-v02.ttf?d5h7ar') format('truetype'),
    url('#{$font-dir-humanitarianicons}Humanitarian-Icons-v02.woff?d5h7ar') format('woff'),
    url('#{$font-dir-humanitarianicons}Humanitarian-Icons-v02.svg?d5h7ar#Humanitarian-Icons-v02') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="humanitarianicons-"], [class*=" humanitarianicons-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'Humanitarian-Icons-v02' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

